/* http://meyerweb.com/eric/tools/css/reset/ 
   v2.0 | 20110126
   License: none (public domain)
*/

html,
body,
div,
span,
applet,
object,
iframe,
h1,
h2,
h3,
h4,
h5,
h6,
p,
blockquote,
pre,
a,
abbr,
acronym,
address,
big,
cite,
code,
del,
dfn,
em,
img,
ins,
kbd,
q,
s,
samp,
small,
strike,
strong,
sub,
sup,
tt,
var,
b,
u,
i,
center,
dl,
dt,
dd,
ol,
ul,
li,
fieldset,
form,
label,
legend,
table,
caption,
tbody,
tfoot,
thead,
tr,
th,
td,
article,
aside,
canvas,
details,
embed,
figure,
figcaption,
footer,
header,
hgroup,
menu,
nav,
output,
ruby,
section,
summary,
time,
mark,
audio,
video {
  margin: 0;
  padding: 0;
  border: 0;
  font-size: 100%;
  font: inherit;
  vertical-align: baseline;
}
/* HTML5 display-role reset for older browsers */
article,
aside,
details,
figcaption,
figure,
footer,
header,
hgroup,
menu,
nav,
section {
  display: block;
}
body {
  line-height: 1;
}
ol,
ul {
  list-style: none;
}
blockquote,
q {
  quotes: none;
}
blockquote:before,
blockquote:after,
q:before,
q:after {
  content: "";
  content: none;
}
table {
  border-collapse: collapse;
  border-spacing: 0;
}

/* Main CSS */

html,
body,
body > div,
#app,
#app > div {
  height: 100%;
}

body {
  background-color: #d9e4dd;
  color: #555555;
  font-family: "Merriweather", serif;
  letter-spacing: 1px;
}

.container {
  background-color: #fbf7f0;
  height: 100%;
}

#info {
  padding: 30px;
}

.contact,
.about,
.education,
.experience {
  font-family: "Montserrat", sans-serif;
  font-size: 1.8rem;
  margin-bottom: 0;
  text-align: center;
  letter-spacing: 3px;
}

.basic-contacts {
  width: 100%;
}

.text-basic-info {
  margin-left: 13px;
  word-wrap: break-word;
  line-height: 1.5;
}

.photo {
  display: flex;
  justify-content: center;
  align-items: center;
}

.about-text {
  line-height: 1.5;
  display: block;
  width: 80%;
  text-align: center;
}

.name {
  font-family: "Montserrat", sans-serif;
  font-size: 3rem;
  font-weight: bold;
  text-align: center;
}

.leftside {
  border-right: 1px solid transparent;
}

@media (min-width: 576px) {
  .leftside {
    border-right: 1px solid #dbdbdb;
  }
}

.education-text,
.experience-text {
  line-height: 1.5;
  text-align: center;
}
